import { every, forEach } from "lodash-es";
import useDragStore from "../drag/dragStore";
import { getCanvasRootElement, hasCoincidence } from "../misc";
import { useSchemaStore } from "../schema";
import useCanvasStore from "./store";
import { GRID_COLUMN_COUNT, GRID_ROW_HEIGHT } from "../constant";


/**
 * 当前拖拽或resize元素的下个坐标是否处于限制区域
 */
export const shouldRestrict = (nextLayout) => {
  return false;
  const { isDragging, dragItem } = useDragStore.getState();
  const { isResizing, resizeItem } = useCanvasStore.getState();
  if (!isDragging && !isResizing) {
    return false;
  }
  const { schema } = useSchemaStore.getState();
  const firstClassChildren = schema.children || [];
  for (const child of firstClassChildren) {
    if (child.id === dragItem?.id || child.id === resizeItem?.id) {
      continue;
    }
    if (hasCoincidence(nextLayout, child.layout)) {
      return true;
    }
  }
  return false;
}

/**
 * 元素挤压变形的布局调整
 * @param id 
 * @param nextLayout
 * 
 * @returns newLayout
 */
export const collapseLayout = (nextLayout, lastLayout) => {
  const { plainSchema, schema} = useSchemaStore.getState();
  const topLayerNodes = schema.children;
  let newNextLayout = { ...nextLayout };
  const { isDragging, dragItem } = useDragStore.getState();
  const { isResizing, resizeItem } = useCanvasStore.getState();
  const id = dragItem?.id || resizeItem?.id;
  // 其他元素的调整
  const changes: any[] = [];

  forEach(topLayerNodes, node => {
    if (node.id === id) {
      return;
    }
    if (!hasCoincidence(newNextLayout, node.layout)) {
      return;
    }
    const isHorizontalBetween = newNextLayout.x >= node.layout.x && newNextLayout.x + newNextLayout.width <= node.layout.x + node.layout.width;
    // 触发水平裁剪
    if (nextLayout.y >= node.layout.y && !isHorizontalBetween) {
      if (newNextLayout.x < node.layout.x) {
        // 在左边
        newNextLayout.width = Math.max(node.layout.x - newNextLayout.x, 1);
      } else {
        // 在右边
        const originalX = newNextLayout.x;
        newNextLayout.x = Math.max(node.layout.x + node.layout.width, 1);  
        newNextLayout.width = Math.max(newNextLayout.width - (newNextLayout.x - originalX), 1);
      }
    } else {
      if (isHorizontalBetween && newNextLayout.y > node.layout.y) {
        newNextLayout = { ...lastLayout };
      }
      if (newNextLayout.y >= node.layout.y + node.layout.height) {
        return;
      }
      // 记录其他元素变更，后续统一执行
      const change = {
        target: node.id,
        type: 'update',
        payload: {
          layout: {
            ...node.layout,
            y: newNextLayout.y + newNextLayout.height,
          }
        }
      };
      changes.push(change); 
    }
  });
  // 派生的其他节点布局调整
  const derivedChanges = [...changes];
  for (let index = 0; index < derivedChanges.length; index++) {
    const change = derivedChanges[index];
    const { layout } = change.payload;

    topLayerNodes.forEach(node => {
      if (node.id === change.target || node.id === id) {
        return;
      }
      if (!hasCoincidence(layout, node.layout)) {
        return;
      }
      const bottom = layout.y + layout.height;
      if (node.layout.y < bottom) {
        derivedChanges.push({
          target: node.id,
          type: 'update',
          derived: true,
          payload: {
            layout: {
              ...node.layout,
              y: bottom,
            }
          }
        })
      }
    })
  };

  return [newNextLayout, derivedChanges];;
}