import { useEffect } from "react";
import { CanvasPlugin } from "../../type";
import evtManager from "../../event";
import { EventType, GRID_ROW_HEIGHT } from "../../constant";
import { useSchemaStore } from "../../schema";
import { throttle } from 'lodash-es';
import useCanvasStore from "../store";
import { useShallow } from "zustand/react/shallow";
import { getCanvasRootElement, runAsync } from "../../misc";

class AdaptivePlugin implements CanvasPlugin {

  heightIncreaseStep = GRID_ROW_HEIGHT;

  render() {

    const updateSchemaNode = useSchemaStore(state => state.updateNode);
    const previewLayout = useCanvasStore(useShallow(state => state.previewLayout));
    const resizeItem = useCanvasStore(state => state.resizeItem);
    const resizeLayout = useSchemaStore(useShallow(state => state.plainSchema[resizeItem?.id]?.layout));
    const plainSchema = useSchemaStore(state => state.plainSchema);
    const applyChanges = useSchemaStore(state => state.applyChanges);

    useEffect(() => {
      const canvasRootElement = getCanvasRootElement();
      const currentRowCount = Math.round(canvasRootElement!.offsetHeight / GRID_ROW_HEIGHT);
      let bottomRowNo = previewLayout.y + previewLayout.height;

      Object.keys(plainSchema).forEach(key => {
        const node = plainSchema[key];
        const bottom = node.layout?.y + node.layout?.height;
        if (bottom) {
          bottomRowNo = Math.max(bottom, bottomRowNo);
        }
      });

      if (bottomRowNo > currentRowCount) {
        const nextHeight = bottomRowNo * GRID_ROW_HEIGHT;
        applyChanges([{
          target: 'CanvasRoot',
          type: 'update',
          payload: {
            style: {
              ...(plainSchema.CanvasRoot.style || {}),
              height: nextHeight,
            }
          }
        }]);
      }

    }, [previewLayout, resizeLayout?.height, resizeLayout?.y, plainSchema]);

    return null;
  }
};

export default AdaptivePlugin;